@import url(https://fonts.google.com/specimen/Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* $minWidth: 320px; */

.grid-container {
  max-width: 1440px;
  display: grid;
  grid-template-areas: "header" "content";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr;
  }

.header {
  height: 80px;
  grid-area: header;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 0.1rem #6d5c5c solid;
}

.header nav {
  padding-left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.header nav li {
  padding-left: 15px;
  padding-right: 15px;
}

.checkList {
  display: -webkit-flex;
  display: flex;
  float: left;
  width: 270px;
  border-right: 0.1rem #6d5c5c solid;
}

.content{
  grid-area: content;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgb(218, 245, 240);
  min-height: 600px;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgb(223, 160, 160);
  float: left;
}


